.popular
{
    display: flex;
    flex-direction: column; 
    align-items: center;
    height: 90vh;
    gap: 10px;
}
.popular h1{
color: #171717;
font-size: 50px;
font-weight: 600;
}
.popular hr{
    width: 250px;
    height: 7px;
    border-radius: 10px;
    background-color: #252525;
}
.popular_item
{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}